.card {
  position: relative;
  padding: 15px 14px;
  width: 200px;
  height: 100%;
  border: 1px solid #f3f3f3;
  box-sizing: border-box;
  border-radius: 5px;
  /*&__photo:hover {
    position: relative;
    height: $card-photo-height * 1.2;
    width: $card-photo-width * 1.2;
    border: 3px solid #f3f3f3;
    box-sizing: border-box;
    padding: 15px 5px 45px;
    margin: 0;
  }*/ }
  .card__photo {
    height: 134px;
    width: 160px;
    margin: 15px 5px 42px; }
  .card__name {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px; }
  .card__code {
    margin-top: 5px; }
    .card__code .code--link {
      text-decoration: none;
      color: #000; }
  .card__review {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    margin: 15px 0 20px; }
    .card__review .review--link {
      text-decoration: none;
      color: #000; }
  .card__price {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 20px; }
    .card__price .price__value {
      font-weight: 700;
      font-size: 16px;
      line-height: 19px; }
